/**
 * Add styles to the notification.
 * Uses https://stylelint.io/user-guide/get-started package
 */
#atc-notification {
  background: #fff;
  border: 1px solid #ccc;
  width: 300px;
  position: fixed;
  right: 0;
  top: 50px;
  z-index: 999;
  transition: transform 0.3s ease-in-out;
  transform: translateX(100%);
}
#atc-notification.show {
  transform: translateX(-15px);
  display: block;
}
#atc-notification.bottom {
  top: auto;
  bottom: 50px;
}
#atc-notification .atc-notification-container {
  position: relative;
  z-index: 1000;
  padding: 15px;
}
#atc-notification .atc-bg-image {
  position: relative;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}
#atc-notification .atc-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 999;
}
#atc-notification .atc-notification-header {
  display: flex;
  justify-content: space-between;
}
#atc-notification .atc-notification-header .atc-notification-title {
  color: #000;
  font-size: 16px;
  text-transform: capitalize;
}
#atc-notification .atc-notification-header .atc-notification-close-btn {
  color: #000;
  cursor: pointer;
  font-size: 22px;
}
#atc-notification .atc-notification-body {
  display: flex;
  padding-top: 10px;
}
#atc-notification .atc-notification-body .atc-notification-product-link {
  max-width: 30%;
  margin-right: 20px;
}
#atc-notification .atc-notification-body .atc-notification-product-name {
  margin: 0;
  font-size: 18px;
  font-weight: 800;
}
#atc-notification .atc-notification-body .atc-notification-product-price {
  margin: 0;
  margin-top: 10px;
  color: #a59a9a;
}
#atc-notification .atc-notification-footer {
  padding-top: 20px;
}
#atc-notification .atc-notification-footer .atc-notification-cart-btn {
  width: 100%;
  display: block;
  padding: 10px 0;
  background-color: #000;
  color: #fff;
  text-align: center;
  text-transform: uppercase;
  font-size: 14px;
}
#atc-notification .atc-notification-footer .atc-notification-cart-btn:hover {
  background-color: #333;
}
#atc-notification.background-image .atc-notification-header .atc-notification-title,
#atc-notification.background-image .atc-notification-body .atc-notification-product-name,
#atc-notification.background-image .atc-notification-body .atc-notification-product-price,
#atc-notification.background-image .atc-notification-header .atc-notification-close-btn {
  color: #fff;
}
#atc-notification.background-image .atc-notification-cart-btn {
  background-color: #fff;
  color: #000;
}
#atc-notification.background-image .atc-notification-cart-btn:hover {
  background-color: #f2f2f2;
}